import React, { lazy, Suspense } from "react";
import { HashRouter as Router, Routes, Route } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";

const Header = lazy(() => import("./components/Header"));
const Footer = lazy(() => import("./components/Footer"));
const Home = lazy(() => import("./pages/Home"));
const GridPage = lazy(() => import("./pages/GridPage"));
const Contato = lazy(() => import("./pages/Contato"));

const basePath = process.env.PUBLIC_URL;

const figures = [
  { nome: "Busto Deadpool", tamanho: "15cm", preco: "59", img: `${basePath}/assets/deadpool.jpg` },
  { nome: "Solo Levelling Igris", tamanho: "15cm", img: `${basePath}/assets/igris.jpg` },
  { nome: "Beru", tamanho: "22cm", img: `${basePath}/assets/beru.jpg` },
  { nome: "Solo Levelling", tamanho: "35cm", img: `${basePath}/assets/sololevelling.jpg` },
  { nome: "Darth Vader", tamanho: "15cm", preco: "59", img: `${basePath}/assets/darth1.jpg` },
  { nome: "Batman", tamanho: "15cm", preco: "59", img: `${basePath}/assets/batman.jpg` },
  { nome: "Majin Boo", tamanho: "15cm", img: `${basePath}/assets/boo.jpg` },
  { nome: "Goku Sem Pintar", tamanho: "15cm", img: `${basePath}/assets/goku.jpg` },
  { nome: "Máscara Demolidor", tamanho: "Grande", img: `${basePath}/assets/mask2.jpg` },
  { nome: "Chaveiros", tamanho: "5cm", img: `${basePath}/assets/chaveiro.jpg` },
  { nome: "Chaveiros", tamanho: "5cm", img: `${basePath}/assets/chaveiro2.jpg` },
  { nome: "Chaveiros", tamanho: "5cm", img: `${basePath}/assets/chaveiro3.jpg` }
];

const decor = [
  { nome: "Cacto", tamanho: "10cm", preco: "50", img: `${basePath}/assets/cacto.jpg` },
  { nome: "Lua com Vela", tamanho: "15cm", img: `${basePath}/assets/lua.jpg` },
  { nome: "Coruja", tamanho: "10cm", preco: "50", img: `${basePath}/assets/coruja.jpg` },
  { nome: "Castelo Harry Potter", tamanho: "8cm", preco: "40", img: `${basePath}/assets/castelo.jpg` },
  { nome: "Vaso Minimalista", img: `${basePath}/assets/minimal.jpg` },
  { nome: "Vaso", img: `${basePath}/assets/vaso1.jpg` },
  { nome: "Vaso Cabeça Groot", img: `${basePath}/assets/groot.jpg` },
  { nome: "Vaso", tamanho: "10cm", img: `${basePath}/assets/vaso2.jpg` }
];

const App = () => (
  <Router>
    <Suspense fallback={<div className="text-white text-center mt-20">Carregando...</div>}>
      <Header />
      <Routes>
        <Route path="/" element={<Home figures={figures} decor={decor} />} />
        <Route path="/figures" element={<GridPage title="Action Figures" data={figures} />} />
        <Route path="/decoracao" element={<GridPage title="Decoração" data={decor} />} />
        <Route path="/contato" element={<Contato />} />
      </Routes>
      <Footer />
    </Suspense>
  </Router>
);

export default App;
